<template>
  <div id="bar-number-services-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsBar: {
        xAxis: {
          axisTick: {
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: "#24395D",
            },
          },
          data: [
            "办案",
            "值班报备",
            "重大安保",
            "街面勤务",
            "备勤报备",
            "可抽调能力",
          ],
          axisLabel: {
            textStyle: {
              color: "#7C85A2",
              fontSize: 12,
              fontWeight: "lighter",
            },
            interval: 0,
            margin: 10,
            formatter: function (value) {
              let ret = "";
              let maxLength = 3;
              let valLength = value.length;
              let rowN = Math.ceil(valLength / maxLength);
              if (rowN > 1) {
                for (var i = 0; i < rowN; i++) {
                  var temp = ""; //每次截取的字符串
                  var start = i * maxLength; //开始截取的位置
                  var end = start + maxLength; //结束截取的位置
                  //这里也可以加一个是否是最后一行的判断，但是不加也没有影响，那就不加吧
                  temp = value.substring(start, end) + "\n";
                  ret += temp; //凭借最终的字符串
                }
                return ret;
              } else {
                return value;
              }
            },
          },
        },
        yAxis: {
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.05)"],
              width: 1,
              type: "solid",
            },
          },
        },
        grid: [
          {
            left: "10%",
            top: "20",
            right: "0%",
            bottom: "40",
          },
        ],
        dataGroupId: "",
        animationDurationUpdate: 500,
        series: {
          type: "bar",
          id: "sales",
          data: [
            {
              value: 100,
              groupId: "办案",
            },
            {
              value: 200,
              groupId: "值班报备",
            },
            {
              value: 300,
              groupId: "重大安保",
            },
            {
              value: 200,
              groupId: "街面勤务",
            },
            {
              value: 300,
              groupId: "备勤报备",
            },
            {
              value: 300,
              groupId: "可抽调能力",
            },
          ],
          universalTransition: {
            enabled: true,
            divideShape: "clone",
          },
          barWidth: 15,

          itemStyle: {
            color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [
              {
                offset: 0,
                color: "rgba(66,119,235,0.10)",
              },
              {
                offset: 1,
                color: "rgba(66,119,235)",
              },
            ]),
            barBorderRadius: [3, 3, 0, 0], // （顺时针左上，右上，右下，左下）
          },
        },
      },
      numbreServicesTimer: null,
    };
  },
  mounted() {
    this.initEchart();
    this.numbreServicesTimer = setInterval(() => {
      this.initEchart();
    }, 3000);
  },
  methods: {
    initEchart() {
      const optionsBar = {
        xAxis: {
          axisTick: {
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: "#24395D",
            },
          },
          data: [
            "办案",
            "值班报备",
            "重大安保",
            "街面勤务",
            "备勤报备",
            "可抽调能力",
          ],
          axisLabel: {
            textStyle: {
              color: "#7C85A2",
              fontSize: 12,
              fontWeight: "lighter",
            },
            interval: 0,
            margin: 10,
            formatter: function (value) {
              let ret = "";
              let maxLength = 3;
              let valLength = value.length;
              let rowN = Math.ceil(valLength / maxLength);
              if (rowN > 1) {
                for (var i = 0; i < rowN; i++) {
                  var temp = ""; //每次截取的字符串
                  var start = i * maxLength; //开始截取的位置
                  var end = start + maxLength; //结束截取的位置
                  //这里也可以加一个是否是最后一行的判断，但是不加也没有影响，那就不加吧
                  temp = value.substring(start, end) + "\n";
                  ret += temp; //凭借最终的字符串
                }
                return ret;
              } else {
                return value;
              }
            },
          },
        },
        yAxis: {
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.05)"],
              width: 1,
              type: "solid",
            },
          },
        },
        grid: [
          {
            left: "10%",
            top: "20",
            right: "0%",
            bottom: "40",
          },
        ],
        dataGroupId: "",
        animationDurationUpdate: 300,
        series: {
          type: "bar",
          id: "sales",
          data: [
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "办案",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "值班报备",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "重大安保",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "街面勤务",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "备勤报备",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "可抽调能力",
            },
          ],
          universalTransition: {
            enabled: true,
            divideShape: "clone",
          },
          barWidth: 15,

          itemStyle: {
            color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [
              {
                offset: 0,
                color: "rgba(66,119,235,0.10)",
              },
              {
                offset: 1,
                color: "rgba(66,119,235)",
              },
            ]),
            barBorderRadius: [3, 3, 0, 0], // （顺时针左上，右上，右下，左下）
          },
        },
      };
      this.optionsBar = optionsBar;
      const barTypeChart = document.querySelector(
        "#bar-number-services-charts"
      );
      if (!barTypeChart) {
        clearInterval(this.numbreServicesTimer);
        return;
      }
      const myChart = echarts.init(barTypeChart);
      myChart.setOption(this.optionsBar);
    },
  },
};
</script>

<style lang="scss" scoped>
#bar-number-services-charts {
  width: 100%;
  height: 220px;
}
</style>