<template>
  <div id="pie-type-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsPie: {
        backgroundColor: "transparent",
        tooltip: {
          trigger: "item",
        },
        visualMap: {
          show: false,
          min: 80,
          max: 600,
          inRange: {
            color: ["#5389ff", "#1b3b331"],
          },
        },
        series: [
          {
            name: "总警力量类型数量",
            type: "pie",
            radius: ["18%", "70%"],
            center: ["45%", "50%"],
            data: [
              { value: 335, name: "警车 335" },
              { value: 310, name: "巡警 310" },
              { value: 274, name: "快反 274" },
              { value: 235, name: "交警 235" },
              { value: 400, name: "开道 400" },
            ].sort(function (a, b) {
              return a.value - b.value;
            }),
            roseType: "radius",
            label: {
              color: "rgba(255, 255, 255, 0.3)",
            },
            labelLine: {
              lineStyle: {
                color: "rgba(255, 255, 255, 0.3)",
              },
              smooth: 0.2,
              length: 10,
              length2: 20,
            },
            itemStyle: {
              color: "#c23531",
              shadowBlur: 200,
              shadowColor: "rgba(0, 0, 0, 0.5)",
            },
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function (idx) {
              return Math.random() * 200;
            },
          },
        ],
      },
    };
  },
  mounted() {
    this.initEchart();
  },
  methods: {
    initEchart() {
      const optionsPie = {
        backgroundColor: "transparent",
        tooltip: {
          trigger: "item",
        },
        visualMap: {
          show: false,
          min: 80,
          max: 600,
          inRange: {
            color: ["#5389ff", "#1b3b331"],
          },
        },
        series: [
          {
            name: "总警力量类型数量",
            type: "pie",
            radius: ["18%", "70%"],
            center: ["45%", "50%"],
            data: [
              { value: 335, name: "警车 335" },
              { value: 310, name: "巡警 310" },
              { value: 274, name: "快反 274" },
              { value: 235, name: "交警 235" },
              { value: 400, name: "开道 400" },
            ].sort(function (a, b) {
              return a.value - b.value;
            }),
            roseType: "radius",
            label: {
              color: "rgba(255, 255, 255, 0.3)",
            },
            labelLine: {
              lineStyle: {
                color: "rgba(255, 255, 255, 0.3)",
              },
              smooth: 0.2,
              length: 10,
              length2: 20,
            },
            itemStyle: {
              color: "#c23531",
              shadowBlur: 200,
              shadowColor: "rgba(0, 0, 0, 0.5)",
            },
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function (idx) {
              return Math.random() * 200;
            },
          },
        ],
      };
      this.optionsPie = optionsPie;
      const pieTypeChart = document.querySelector("#pie-type-charts");
        if (!pieTypeChart) {
        clearInterval(this.trendServiceTimer);
        return;
      }
      const myChart = echarts.init(pieTypeChart);
      myChart.setOption(this.optionsPie);
    },
  },
};
</script>

<style lang="scss" scoped>
#pie-type-charts {
  width: 100%;
  height: 230px;
}
</style>