<template>
  <div id="pie-warn-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsPie: {
        tooltip: {
          trigger: "item",
        },
        legend: {
          type: "scroll",
          orient: "vertical",
          right: 10,
          top: 80,
          bottom: 20,
          itemWidth: 12,
          itemHeight: 12,
          icon: "circle",
          textStyle: {
            color: "#7C85A2",
          },
        },
        color: [
          new echarts.graphic.LinearGradient(0, 1, 1, 0, [
            {
              offset: 0,
              color: "rgba(66,119,235,0.1)",
            },
            {
              offset: 1,
              color: "rgba(66,119,235,1)",
            },
          ]),
          new echarts.graphic.LinearGradient(0, 1, 1, 0, [
            {
              offset: 0,
              color: "rgba(255,189,46,0.1)",
            },
            {
              offset: 1,
              color: "rgba(255,189,46,1)",
            },
          ]),
          new echarts.graphic.LinearGradient(0, 1, 1, 0, [
            {
              offset: 0,
              color: "rgba(0,190,170,0.1)",
            },
            {
              offset: 1,
              color: "rgba(0,190,170,1)",
            },
          ]),
        ],
        series: [
          {
            name: "报警类型统计",
            type: "pie",
            radius: ["30%", "60%"],
            center: ["40%", "45%"],
            avoidLabelOverlap: false,
            itemStyle: {
              borderColor: "none",
              borderWidth: 1,
            },
            label: {
              show: false,
              position: "center",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "40",
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: false,
            },
            data: [
              { value: 1048, name: "民警   1048" },
              { value: 735, name: "协警   735" },
              { value: 580, name: "警车   580" },
            ],
          },
        ],
      },
    };
  },
  mounted() {
    this.initEchart();
  },
  methods: {
    initEchart() {
      const pieWarnChart = document.querySelector("#pie-warn-charts");
      const myChart = echarts.init(pieWarnChart);
      myChart.setOption(this.optionsPie);
    },
  },
};
</script>

<style lang="scss" scoped>
#pie-warn-charts {
  width: 100%;
  height: 220px;
}
</style>