<template>
  <div id="pie-type-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsPie: {
        backgroundColor: "transparent",
        tooltip: {
          trigger: "item",
        },
        visualMap: {
          show: false,
          min: 80,
          max: 500,
          inRange: {
            color: ["#5389FF", "#1B3B81","#5389FF"],
          },
        },
        series: [
          {
            name: "总警力量类型数量",
            type: "pie",
            radius: ["20%", "70%"],
            center: ["55%", "50%"],
            data: [
              { value: 200, name: "一级 200" },
              { value: 410, name: "二级 410" },
              { value: 500, name: "三级 500" },
            ].sort(function (a, b) {
              return a.value - b.value;
            }),
            roseType: "radius",
            label: {
              color: "rgba(255, 255, 255, 0.3)",
            },
            labelLine: {
              lineStyle: {
                color: "rgba(255, 255, 255, 0.3)",
              },
              smooth: 0.2,
              length: 10,
              length2: 20,
            },
            itemStyle: {
              color: "#c23531",
              shadowBlur: 200,
              shadowColor: "rgba(0, 0, 0, 0.5)",
            },
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function (idx) {
              return Math.random() * 200;
            },
          },
        ],
      },
    };
  },
  mounted() {
    this.initEchart();
  },
  methods: {
    initEchart() {
      const pieTypeChart = document.querySelector("#pie-type-charts");
      const myChart = echarts.init(pieTypeChart);
      myChart.setOption(this.optionsPie);
    },
  },
};
</script>

<style lang="scss" scoped>
#pie-type-charts {
  width: 100%;
  height: 230px;
}
</style>