<template>
  <div id="bar-number-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsNumberBar: {
        tooltip: {
          trigger: "item",
        },
        grid: [
          {
            left: "12%",
            top: "50",
            right: "12%",
            bottom: "20",
          },
        ],
        xAxis: {
          axisTick: {
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: "#24395D",
            },
          },
          axisLabel: {
            interval: 0,
            color: "#7C85A2",
            textStyle: {
              color: "#7C85A2",
              fontSize: 12,
              fontWeight: "lighter",
            },
          },
          data: ["金牛区", "成华区", "武侯区", "锦江区", "青羊区", "高新区"],
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.05)"],
              width: 1,
              type: "solid",
            },
          },
        },
        dataGroupId: "",
        animationDurationUpdate: 500,
        series: {
          type: "bar",
          id: "sales",
          barWidth: 12,
          data: [
            {
              value: 20,
              groupId: "金牛区",
            },
            {
              value: 30,
              groupId: "成华区",
            },
            {
              value: 10,
              groupId: "武侯区",
            },
            {
              value: 15,
              groupId: "锦江区",
            },
            {
              value: 25,
              groupId: "青羊区",
            },
            {
              value: 15,
              groupId: "高新区",
            },
          ],
          universalTransition: {
            enabled: true,
            divideShape: "clone",
          },
          itemStyle: {
            color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [
              {
                offset: 0,
                color: "#172F61",
              },
              {
                offset: 1,
                color: "#4277EB ",
              },
            ]),
            // color: "#FFBD2E",
            barBorderRadius: [3, 3, 0, 0], // （顺时针左上，右上，右下，左下）
          },
        },
      },
      // 设置定时器
      timer:null,
    };
  },
  mounted() {
    this.initEchart();
    this.timer = setInterval(() => {
      this.initEchart();
    }, 3000);
  },
  update() {
  },
  watch: {
    $route: {
      handler: function (val, oldVal) {
      },
      // 深度观察监听
      deep: true,
    },
  },
  methods: {
    initEchart() {
      const optionsNumberBar = {
        tooltip: {
          trigger: "item",
        },
        grid: [
          {
            left: "12%",
            top: "50",
            right: "12%",
            bottom: "20",
          },
        ],
        xAxis: {
          axisTick: {
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: "#24395D",
            },
          },
          axisLabel: {
            interval: 0,
            color: "#7C85A2",
            textStyle: {
              color: "#7C85A2",
              fontSize: 12,
              fontWeight: "lighter",
            },
          },
          data: ["金牛区", "成华区", "武侯区", "锦江区", "青羊区", "高新区"],
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.05)"],
              width: 1,
              type: "solid",
            },
          },
        },
        dataGroupId: "",
        animationDurationUpdate: 500,
        series: {
          type: "bar",
          id: "sales",
          barWidth: 12,
          data: [
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "金牛区",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "成华区",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "武侯区",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "锦江区",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "青羊区",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "高新区",
            },
          ],
          universalTransition: {
            enabled: true,
            divideShape: "clone",
          },
          itemStyle: {
            color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [
              {
                offset: 0,
                color: "#172F61",
              },
              {
                offset: 1,
                color: "#4277EB ",
              },
            ]),
            // color: "#FFBD2E",
            barBorderRadius: [3, 3, 0, 0], // （顺时针左上，右上，右下，左下）
          },
        },
      };
      this.optionsNumberBar = optionsNumberBar;
      const barTypeChart = document.querySelector("#bar-number-charts");
      if(!barTypeChart){
        clearInterval(this.timer)
        return 
      }
      const myChart = echarts.init(barTypeChart);
      myChart.setOption(this.optionsNumberBar);
    },
  },
};
</script>

<style lang="scss" scoped>
#bar-number-charts {
  width: 100%;
  height: 210px;
}
</style>