<template>
  <div id="line-trend-service-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsBar: {
        tooltip: {
          trigger: "item",
        },
        color: ["#F64837", "#BA963F", "#13C665"],
        legend: {
          data: ["民警", "协警", "警车"],
          // lineStyle: ["#F64837", "#BA963F", "#13C665"],
          textStyle: {
            color: "#fff",
          },
          left: "right",
        },
        grid: [
          {
            left: "10%",
            top: "50",
            right: "0%",
            bottom: "40",
          },
        ],
        xAxis: {
          type: "category",
          data: ["1", "4", "8", "12", "16", "20", "24"],
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,0.1)",
            },
          },
          axisLabel: {
            color: "#fff",
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.1)"],
              width: 1,
              type: "solid",
            },
          },
        },
        series: [
          {
            name: "民警",
            symbol: "none",
            data: [
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
            ],
            type: "line",
            smooth: true,
            areaStyle: {
              color: "rgba(178,59,59,0.30)",
            },
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#F64837",
                },
              },
            },
          },
          {
            name: "协警",
            symbol: "none",
            data: [
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
            ],
            type: "line",
            smooth: true,
            areaStyle: {
              color: "rgba(255,189,46,0.30)",
            },
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#BA963F",
                },
              },
            },
          },
          {
            name: "警车",
            symbol: "none",
            data: [
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
            ],
            type: "line",
            smooth: true,
            areaStyle: {
              color: "rgba(17,180,102,0.30)",
            },
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#13C665",
                },
              },
            },
          },
        ],
      },
    };
  },
  mounted() {
    this.initEchart();
    this.trendServiceTimer = setInterval(() => {
      this.initEchart();
    }, 3000);
  },
  methods: {
    initEchart() {
      const optionsBar = {
        tooltip: {
          trigger: "item",
        },
        color: ["#F64837", "#BA963F", "#13C665"],
        legend: {
          data: ["民警", "协警", "警车"],
          // lineStyle: ["#F64837", "#BA963F", "#13C665"],
          textStyle: {
            color: "#fff",
          },
          left: "right",
        },
        grid: [
          {
            left: "10%",
            top: "50",
            right: "0%",
            bottom: "40",
          },
        ],
        xAxis: {
          type: "category",
          data: ["1", "4", "8", "12", "16", "20", "24"],
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,0.1)",
            },
          },
          axisLabel: {
            color: "#fff",
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.1)"],
              width: 1,
              type: "solid",
            },
          },
        },
        series: [
          {
            name: "民警",
            symbol: "none",
            data: [
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
            ],
            type: "line",
            smooth: true,
            areaStyle: {
              color: "rgba(178,59,59,0.30)",
            },
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#F64837",
                },
              },
            },
          },
          {
            name: "协警",
            symbol: "none",
            data: [
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
            ],
            type: "line",
            smooth: true,
            areaStyle: {
              color: "rgba(255,189,46,0.30)",
            },
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#BA963F",
                },
              },
            },
          },
          {
            name: "警车",
            symbol: "none",
            data: [
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
              Math.round((Math.random() + 1) * 10),
            ],
            type: "line",
            smooth: true,
            areaStyle: {
              color: "rgba(17,180,102,0.30)",
            },
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#13C665",
                },
              },
            },
          },
        ],
      };
      this.optionsBar = optionsBar;

      const lineTrendChart = document.querySelector(
        "#line-trend-service-charts"
      );
      if (!lineTrendChart) {
        clearInterval(this.trendServiceTimer);
        return;
      }
      const myChart = echarts.init(lineTrendChart);
      myChart.setOption(this.optionsBar);
    },
  },
};
</script>

<style lang="scss" scoped>
#line-trend-service-charts {
  width: 100%;
  height: 220px;
}
</style>