<template>
  <div id="bar-type-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsBar: {
        tooltip: {
          trigger: "item",
        },
        xAxis: {
          axisTick: {
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: "#24395D",
            },
          },
          axisLabel: {
            textStyle: {
              color: "#7C85A2",
              fontSize: 12,
              fontWeight: "lighter",
            },
            interval: 0,
            rotate: 30,
            margin: 15,
          },
          data: [
            "刑事类",
            "治安类",
            "道路交通类警情",
            "群体性事件",
            "灾害事故类",
            "其他违法犯罪",
          ],
        },
        yAxis: {
          // 设置网格线样式
          splitLine: {
            show: true,
            lineStyle: {
              color: ["#24395D"],
              width: 1,
              type: "solid",
            },
          },
        },
        dataGroupId: "",
        animationDurationUpdate: 500,
        series: {
          type: "bar",
          id: "sales",
          data: [
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "刑事类",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "治安类",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "道路交通类警情",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "群体性事件",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "灾害事故类",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "其他违法犯罪",
            },
          ],
          barWidth: 12,
          universalTransition: {
            enabled: true,
            divideShape: "clone",
          },
          itemStyle: {
            color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [
              {
                offset: 0,
                color: "#323743",
              },
              {
                offset: 1,
                color: "#FFBD2E",
              },
            ]),
            barBorderRadius: [3, 3, 0, 0], // （顺时针左上，右上，右下，左下）
          },
        },
      },
      // 设置定时器
      typeTimer: null,
    };
  },
  mounted() {
    this.initEchart();
    this.typeTimer = setInterval(() => {
      this.initEchart();
    }, 3000);
  },
  methods: {
    initEchart() {
      const optionsBar = {
        tooltip: {
          trigger: "item",
        },
        xAxis: {
          axisTick: {
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: "#24395D",
            },
          },
          axisLabel: {
            textStyle: {
              color: "#7C85A2",
              fontSize: 12,
              fontWeight: "lighter",
            },
            interval: 0,
            rotate: 30,
            margin: 15,
          },
          data: [
            "刑事类",
            "治安类",
            "道路交通类警情",
            "群体性事件",
            "灾害事故类",
            "其他违法犯罪",
          ],
        },
        yAxis: {
          // 设置网格线样式
          splitLine: {
            show: true,
            lineStyle: {
              color: ["#24395D"],
              width: 1,
              type: "solid",
            },
          },
        },
        dataGroupId: "",
        animationDurationUpdate: 500,
        series: {
          type: "bar",
          id: "sales",
          data: [
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "刑事类",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "治安类",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "道路交通类警情",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "群体性事件",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "灾害事故类",
            },
            {
              value: Math.round((Math.random() + 1) * 10),
              groupId: "其他违法犯罪",
            },
          ],
          barWidth: 12,
          universalTransition: {
            enabled: true,
            divideShape: "clone",
          },
          itemStyle: {
            color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [
              {
                offset: 0,
                color: "#323743",
              },
              {
                offset: 1,
                color: "#FFBD2E",
              },
            ]),
            barBorderRadius: [3, 3, 0, 0], // （顺时针左上，右上，右下，左下）
          },
        },
      };
      this.optionsBar = optionsBar;
      const barTypeChart = document.querySelector("#bar-type-charts");
      if (!barTypeChart) {
        clearInterval(this.typeTimer);
        return;
      }
      const myChart = echarts.init(barTypeChart);
      myChart.setOption(this.optionsBar);
    },
  },
};
</script>

<style lang="scss" scoped>
#bar-type-charts {
  width: 100%;
  height: 325px;
}
</style>