<template>
  <div id="bar-service-number-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsBar: {
        xAxis: {
          axisTick: {
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: "#24395D",
            },
          },
          data: ["民警", "警车", "协警"],
          axisLabel: {
            textStyle: {
              color: "#7C85A2",
              fontSize: 12,
              fontWeight: "lighter",
            },
            interval: 0,
            margin: 15,
          },
        },
        yAxis: {
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.1)"],
              width: 1,
              type: "solid",
            },
          },
        },
        grid: [
          {
            left: "10%",
            top: "20",
            right: "10%",
            bottom: "30",
          },
        ],
        dataGroupId: "",
        animationDurationUpdate: 500,
        series: {
          type: "bar",
          id: "sales",
          data: [
            {
              value: 100,
              groupId: "民警",
            },
            {
              value: 200,
              groupId: "警车",
            },
            {
              value: 300,
              groupId: "协警",
            },
          ],
          universalTransition: {
            enabled: true,
            divideShape: "clone",
          },
          barWidth: 25,

          itemStyle: {
            color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [
              {
                offset: 0,
                color: "rgba(66,119,235,0.10)",
              },
              {
                offset: 1,
                color: "rgba(66,119,235)",
              },
            ]),
            barBorderRadius: [3, 3, 0, 0], // （顺时针左上，右上，右下，左下）
          },
        },
      },
      numberServiceTimer: null,
    };
  },
  mounted() {
    this.initEchart();
    this.numberServiceTimer = setInterval(() => {
      this.initEchart();
    }, 3000);
  },
  methods: {
    initEchart() {
      const optionsBar = {
        xAxis: {
          axisTick: {
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: "#24395D",
            },
          },
          data: ["民警", "警车", "协警"],
          axisLabel: {
            textStyle: {
              color: "#7C85A2",
              fontSize: 12,
              fontWeight: "lighter",
            },
            interval: 0,
            margin: 15,
          },
        },
        yAxis: {
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.1)"],
              width: 1,
              type: "solid",
            },
          },
        },
        grid: [
          {
            left: "10%",
            top: "20",
            right: "10%",
            bottom: "30",
          },
        ],
        dataGroupId: "",
        animationDurationUpdate: 500,
        series: {
          type: "bar",
          id: "sales",
          data: [
            {
              value: Math.round((Math.random() + 1) * 100),
              groupId: "民警",
            },
            {
              value: Math.round((Math.random() + 1) * 100),
              groupId: "警车",
            },
            {
              value: Math.round((Math.random() + 1) * 100),
              groupId: "协警",
            },
          ],
          universalTransition: {
            enabled: true,
            divideShape: "clone",
          },
          barWidth: 25,

          itemStyle: {
            color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [
              {
                offset: 0,
                color: "rgba(66,119,235,0.10)",
              },
              {
                offset: 1,
                color: "rgba(66,119,235)",
              },
            ]),
            barBorderRadius: [3, 3, 0, 0], // （顺时针左上，右上，右下，左下）
          },
        },
      };
      this.optionsBar = optionsBar;
      const barServiceTypeChart = document.querySelector(
        "#bar-service-number-charts"
      );
      if (!barServiceTypeChart) {
        clearInterval(this.numberServiceTimer);
        return;
      }
      const myChart = echarts.init(barServiceTypeChart);
      myChart.setOption(this.optionsBar);
    },
  },
};
</script>

<style lang="scss" scoped>
#bar-service-number-charts {
  width: 100%;
  height: 195px;
}
</style>